import {getConfig} from './httpClient';

export const createOriginServices = (fetchRequest: any) => ({
  async getOrigin(origin: string, accessToken: string) {
    const endPoint = `/services/uaa/api/organizations/${origin}`;
    try {
      const data = await fetchRequest({
        url: getConfig().BASE_URL + endPoint,
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + accessToken,
        },
      });
      return Promise.resolve(data?.data);
    } catch (error) {
      return Promise.reject(error);
    }
  },

  async getListAccount(dataFilter?: any, params?: any) {
    let body = {
      advanced: false,
      contactId: '',
      custId: null,
      logins: [],
      field: '',
      from: '',
      keyWord: '',
      orgIn: null,
      owner: '',
      q: '',
      recipientId: '',
      recipientRole: [],
      refId: '',
      resource: 'table_user',
      terms: {},
      to: '',
    };
    if (dataFilter) {
      body = {
        ...body,
        ...dataFilter,
      };
    }
    const endPoint = '/services/uaa/api/search/user-info/role';
    try {
      const data = await fetchRequest({
        url: getConfig().BASE_URL + endPoint,
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(body),
        params: params,
      });
      return Promise.resolve({
        data: data?.data,
        totalRecords: Number(data?.headers.get('x-total-count')),
      });
    } catch (error) {
      Promise.reject(error);
    }
  },
});
