import {Divider} from '@ui-kitten/components';
import HeaderCM from '../../../Header/HeaderCM';
import {useEffect, useState} from 'react';
import useDebounce from '../../../../../hooks/useDebounce';
import {FlatList, View} from 'react-native';
import ButtonCM from '../../../Button';
import UserItem from '../user';
import TextFieldCM from '../../../TextField';
import {IUserInfo} from '../../../../../model/interface/service.interface';
import {useOriginServices} from '../../../../../hooks/apiHooks/useOriginServices';
import {useTranslation} from 'react-i18next';

type Props = {
  onClose: () => void;
  filter?: any;
  multiple?: boolean;
  onSelect: (listUserSelected: IUserInfo[] | IUserInfo) => void;
  dataSelected: IUserInfo[] | IUserInfo;
};

const ListUserSelect = ({
  onClose,
  filter,
  multiple,
  onSelect,
  dataSelected,
}: Props) => {
  const {t} = useTranslation();
  const [textSearch, setTextSearch] = useState('');
  const textSearchDebounce = useDebounce(textSearch, 500);

  const [listUser, setListUser] = useState<IUserInfo[]>([]);
  const [listUserSelected, setListUserSelected] = useState<IUserInfo[]>(
    !dataSelected
      ? []
      : Array.isArray(dataSelected)
      ? dataSelected
      : [dataSelected],
  );
  const [page, setPage] = useState(0);
  const [totalPage, setTotalPage] = useState(0);
  const [refreshing, setRefreshing] = useState(false);
  const OrginService = useOriginServices();

  useEffect(() => {
    getListUser(0);
  }, [textSearchDebounce]);

  const getListUser = async (page: number) => {
    try {
      const params = {
        page: page,
        size: 50,
      };
      const body = {
        ...filter,
        q: textSearchDebounce.trim(),
      };
      const dataResp = await OrginService.getListAccount(body, params);
      if (dataResp) {
        if (page === 0) {
          setListUser(dataResp.data);
        } else {
          setListUser([...listUser, ...dataResp.data]);
        }
        setTotalPage(dataResp.totalRecords);
      }
    } catch (error) {
    } finally {
      setRefreshing(false);
    }
  };

  const onRefresh = () => {
    setRefreshing(true);
    setPage(0);
    getListUser(0);
  };

  const fetchMore = () => {
    if (listUser?.length < totalPage) {
      setPage(page + 1);
      getListUser(page + 1);
    }
  };

  const handleSubmit = () => {
    if (multiple) {
      onSelect(listUserSelected);
    } else {
      onSelect(listUserSelected[0]);
    }
    onClose?.();
  };

  return (
    <>
      <HeaderCM
        style={{
          backgroundColor: '#fff',
        }}
        contentStyle={{
          backgroundColor: '#fff',
        }}
        titleStyle={{
          color: '#000810',
        }}
        fillIconBackLeft="#000810"
        onPressIconLeft={onClose}
      />
      <Divider />

      <View style={{padding: 10, flex: 1}}>
        <TextFieldCM
          style={{height: 30}}
          value={textSearch}
          onChangeText={setTextSearch}
          label={t('CM.search')}
        />
        <FlatList
          refreshing={refreshing}
          onRefresh={onRefresh}
          onEndReachedThreshold={0.5}
          onEndReached={fetchMore}
          style={{flex: 1, marginTop: 10}}
          contentContainerStyle={{rowGap: 16}}
          data={listUser}
          renderItem={({item}) => (
            <UserItem
              listUserSelected={listUserSelected}
              setListUserSelected={setListUserSelected}
              userData={item}
              multiple={multiple}
            />
          )}
        />
      </View>
      <ButtonCM
        style={{marginHorizontal: 10}}
        label={t('CM.confirm')}
        onPress={handleSubmit}
      />
    </>
  );
};

export default ListUserSelect;
