import {Image, TouchableOpacity, View} from 'react-native';
import TextCM from '../../../Text';
import {IUserInfo} from '../../../../../model/interface/service.interface';
import {String} from '../../../../../constants';
import {useAppSelector} from '../../../../../hooks/useRedux';
import {Dispatch, SetStateAction} from 'react';
import {Icon} from '@ui-kitten/components';

type Props = {
  userData: IUserInfo;
  setListUserSelected: Dispatch<SetStateAction<IUserInfo[]>>;
  listUserSelected: IUserInfo[];
  multiple?: boolean;
};

const UserItem = ({
  userData,
  setListUserSelected,
  listUserSelected,
  multiple,
}: Props) => {
  const {dataAccount} = useAppSelector(state => state.account);

  const handleSelect = () => {
    if (multiple) {
      const dataInList = listUserSelected?.find(
        user => user?.email === userData.email,
      );
      if (dataInList) {
        const listFilter = listUserSelected?.filter(
          user => user?.email !== userData.email,
        );
        setListUserSelected([...listFilter]);
      } else {
        setListUserSelected([...listUserSelected, userData]);
      }
    } else {
      setListUserSelected([userData]);
    }
  };

  const checkDataInList = () => {
    return listUserSelected.find(user => user?.email === userData.email);
  };
  return (
    <TouchableOpacity
      onPress={handleSelect}
      disabled={userData.email === dataAccount.email}
      style={{
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'space-between',
        padding: 10,
        backgroundColor:
          userData.email === dataAccount.email ? '#E8E8E8' : '#fff',
      }}>
      <View
        style={{
          flexDirection: 'row',
          columnGap: 10,
          alignItems: 'center',
        }}>
        <Image
          style={{
            width: 32,
            height: 32,
            borderRadius: 16,
          }}
          source={{uri: userData?.imageUrl || String.DEFAULT_IMAGE}}
        />
        <View>
          <TextCM>{userData?.personalName}</TextCM>
          <TextCM>{userData?.email}</TextCM>
        </View>
      </View>
      {checkDataInList() && (
        <Icon
          name="checkmark-circle-2-outline"
          fill="green"
          width={24}
          height={24}
        />
      )}
    </TouchableOpacity>
  );
};

export default UserItem;
