import {Pressable, StyleProp, TextStyle, View, ViewStyle} from 'react-native';
import TextCM from '../Text';
import styles from './styles';
import ModalFullScreen from '../ModalFullScreen';
import {useState} from 'react';
import ListUserSelect from './component/list';
import {IUserInfo} from '../../../model/interface/service.interface';

type Props = {
  label: string;
  isRequired?: boolean;
  style?: StyleProp<ViewStyle>;
  labelStyle?: StyleProp<TextStyle>;
  containerStyle?: StyleProp<ViewStyle>;
  placeholder?: string;
  filter?: any;
  multiple?: boolean;
  dataSelected: IUserInfo[] | IUserInfo;
  setDataSelected: (data: IUserInfo[] | IUserInfo) => void;
};

const ListUser = ({
  label,
  isRequired,
  style,
  labelStyle,
  containerStyle,
  placeholder,
  filter,
  multiple = false,
  dataSelected,
  setDataSelected,
}: Props) => {
  const [isVisible, setIsVisible] = useState(false);

  const getViewData = () => {
    if (!dataSelected) {
      return <TextCM style={styles.placeholderTxt}>{placeholder}</TextCM>;
    }
    if (!multiple && !Array.isArray(dataSelected)) {
      return (
        <TextCM style={styles.placeholderTxt}>{dataSelected?.email}</TextCM>
      );
    }
    if (multiple && Array.isArray(dataSelected)) {
      return (
        <TextCM numberOfLines={1}>
          {dataSelected?.map((i: IUserInfo) => i?.email).join(', ')}
        </TextCM>
      );
    }
  };
  return (
    <View style={[{rowGap: 10, backgroundColor: '#fff'}, style]}>
      <TextCM style={[styles.label, labelStyle]}>
        {label} {isRequired && <TextCM style={styles.colorRequired}>*</TextCM>}
      </TextCM>
      <Pressable
        onPress={() => setIsVisible(true)}
        style={[styles.container, containerStyle]}>
        {getViewData()}
      </Pressable>

      <ModalFullScreen
        isVisible={isVisible}
        children={
          <ListUserSelect
            multiple={multiple}
            filter={filter}
            onClose={() => setIsVisible(false)}
            onSelect={setDataSelected}
            dataSelected={dataSelected}
          />
        }
      />
    </View>
  );
};

export default ListUser;
