import {Image, ScrollView, View} from 'react-native';
import {Color, String} from '../../../../../constants';
import HeaderCM from '../../../../components/Header/HeaderCM';
import InputCM from '../../../review-eid/components/InputCM';
import {CheckBox} from '@ui-kitten/components';
import {useEffect, useState} from 'react';
import InputDate from '../../../review-econtract-flow/components/InputDate';
import TextCM from '../../../../components/Text';
import {
  ITicket,
  IUserInfo,
} from '../../../../../model/interface/service.interface';
import {useTicketServices} from '../../../../../hooks/apiHooks/useTicketServices';
import {useAppSelector} from '../../../../../hooks/useRedux';
import ButtonCM from '../../../../components/Button';
import ListUser from '../../../../components/ListUser';
import {isEmpty} from 'lodash';
import Toast from 'react-native-toast-message';
import {useTranslation} from 'react-i18next';
import styles from './styles';

type Props = {
  onClose: () => void;
  dataTicket: ITicket;
  toogleReloadData: () => void;
};

const ForwardModal = ({onClose, dataTicket, toogleReloadData}: Props) => {
  const {t} = useTranslation();
  const [checked, setChecked] = useState(false);
  const [userForward, setUserForward] = useState<IUserInfo>();
  const [userSelected, setUserSelected] = useState<IUserInfo>();
  const [isLoading, setLoading] = useState(false);
  const {dataAccount} = useAppSelector(state => state.account);
  const TicketService = useTicketServices();

  useEffect(() => {
    getUserForward();
  }, []);

  const getUserForward = async () => {
    const dataResp = await TicketService.getMultiLoginAccount([
      dataAccount.email || '',
    ]);

    if (dataResp) {
      setUserForward(dataResp[0]);
    }
  };

  const handleSubmit = async () => {
    try {
      setLoading(true);
      const body = {
        ticketId: dataTicket.resourceId?.substr(4),
        recipientId: null,
        contactId: null,
        email: userSelected?.email,
        name: userSelected?.personalName,
        phone: userSelected?.phone,
        reason: 'null',
      };
      const data = await TicketService.forwaredTicket(
        dataTicket.resourceId?.substr(4),
        dataTicket.recipientId,
        dataTicket.implementId,
        body,
      );
      if (data) {
        Toast.show({
          type: 'success',
          text1: t('CM.info'),
          text2: t('TK.authorize.success'),
        });
        onClose();
        toogleReloadData();
      } else {
        Toast.show({
          type: 'error',
          text1: t('CM.info'),
          text2: t('TK.authorize.error'),
        });
      }
    } catch (error) {
    } finally {
      setLoading(false);
    }
  };

  return (
    <>
      <HeaderCM
        style={{
          backgroundColor: '#fff',
        }}
        contentStyle={{
          backgroundColor: '#fff',
          borderBottomColor: Color.borderColor,
          borderBottomWidth: 1,
        }}
        titleStyle={{
          color: '#000810',
        }}
        fillIconBackLeft="#000810"
        title={t('TK.authorize.title')}
        onPressIconLeft={onClose}
      />

      <ScrollView
        contentContainerStyle={{
          rowGap: 10,
        }}
        style={{paddingHorizontal: 16, marginVertical: 10}}>
        <InputCM
          onChangeText={() => {}}
          label={t('TK.authorize.name')}
          value={dataTicket?.resourceName || ''}
          disabled
        />
        {/* 
        <CheckBox checked={checked} onChange={setChecked}>
          Thi gian t ng y quyn khi nhn c yu cu tng t
        </CheckBox>

        <View>
          <InputDate label="T ngy" isRequired />
          <InputDate label="n ngy" isRequired />
        </View> */}

        <View style={{rowGap: 10}}>
          <TextCM>{t('TK.authorize.person')}</TextCM>
          <View style={styles.owner}>
            <Image
              style={styles.img}
              source={{uri: userForward?.imageUrl || String.DEFAULT_IMAGE}}
            />
            <View>
              <TextCM>{userForward?.personalName}</TextCM>
              <TextCM>{userForward?.email}</TextCM>
            </View>
          </View>
        </View>

        <ListUser
          multiple={false}
          label={t('TK.authorize.person-authorize')}
          isRequired={true}
          placeholder={t('TK.authorize.person-authorize-placeholder')}
          dataSelected={userSelected as any}
          setDataSelected={setUserSelected as any}
          filter={{orgIn: dataAccount.orgIn}}
        />
      </ScrollView>

      <View style={styles.footer}>
        <ButtonCM
          style={{flex: 1, backgroundColor: Color.borderColor}}
          styleLabel={{color: Color.Text}}
          label={t('CM.labelCancel')}
          onPress={onClose}
        />
        <ButtonCM
          disabled={isEmpty(userSelected) ? true : false}
          style={{flex: 1}}
          label={t('CM.confirm')}
          loading={isLoading}
          onPress={handleSubmit}
        />
      </View>
    </>
  );
};

export default ForwardModal;
